<template>
  <div class="page-license" v-loading="loading">
    <page-header />

    <company
      :licenseInfo="licenseInfo"
    />

    <license-info
      :list="licenseInfo.licensePermitListVo"
    />

    <footer-tips />
  </div>
</template>

<script>
import { getLicense } from 'models/Api';

import PageHeader from 'components/Sub/Header';
import Company from 'components/License/Company';
import LicenseInfo from 'components/License/LicenseInfo';
import FooterTips from 'components/Sub/Tips/Footer';

export default {
  name: 'License',
  data () {
    return {
      licenseNumber: this.$route.params.id, // 91440101MA5CLGPK81
      licenseInfo: {}, // 营业执照详情
      loading: false,
    }
  },
  components: {
    PageHeader,
    Company,
    LicenseInfo,
    FooterTips
  },
  methods: {
    async getLicense () {
      const licenseNumber = this.licenseNumber;

      // 页面参数错误
      // 跳转到404页面
      if (!licenseNumber) {
        this.$router.push({
          name: '404',
          query: {
            from: this.$route.fullPath
          }
        });
        return;
      }

      this.loading = true;

      const {
        code,
        message,
        data
      } = await getLicense(licenseNumber);
      
      if (code !== 0 || !data || JSON.stringify(data) === '{}') {
        this.$router.push({
          name: '404',
          query: {
            from: this.$route.fullPath
          }
        });
        return;
      } else {
        this.licenseInfo = data;
      }

      this.loading = false;
    }
  },
  created () {
    this.getLicense();
  }
}
</script>
